.about__container{
display: grid;
   grid-template-columns: 35% 50%;
   gap: 15%; 
   margin-top: 5rem !important;
} 

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary),transparent);
    display: grid;
    place-items: center;
    overflow: hidden;
} 


.about__me_image {
    border-radius: 2rem;
    overflow: hidden;
    transition: var(--transition);
    width: 180px;
}

.about__me_image:hover{
   transform: rotate(0);
} 

.about__sub_content {
    display: flex;
}

.about__sub_content h5 {
    color: var(--color-white);
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    margin: 0.2rem;
}

.about__card:hover{
    background: transparent;
    border-color:transparent ;
    cursor: default;
    border-color: var(--color-primary-variant);
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__bottom_content  p{
    margin: 2rem 0.2rem;
    color: var(--color-light);
}


/* ========================== MEDIA QUERY (MEDIUM DEVICE) =========================== */

@media screen and (max-width: 1024px){
    .about__container {
        grid-template-columns: 1fr;
    }
    .about__sub_content {
        display: flex;
        justify-content: space-evenly;
    }
}

/* ========================== MEDIA QUERY (PHONE DEVICE) =========================== */
@media screen and (max-width: 768px){
    .about__container {
        gap: 8rem;
    }
    .about__me {
        margin: auto;
        width: 50%;
    }
    .about__bottom_content {
        text-align: center;
    }
}

/* ========================== MEDIA QUERY (TAB DEVICE) =========================== */

@media screen and (max-width: 600px){
    
    section{
        margin-top: 3rem;
    }
    .about__me{
        width: 95%;
        margin: auto;
    }
    

    .about__sub_content {
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
    }
}





