/* ========================== MEDIA QUERY (SMALl DEVICE) =========================== */

@media screen and (max-width: 768px) {
  section {
    margin-top: 5rem;
}
  header {
    height: 62vh;
    margin-top: 3rem; 
     
  }
  

  .header__socials {
    top: 65%;
    left: 5%;
    /* bottom: 3rem; */
  }

  .scroll__down{
    top: 65%;
    right: 7%;
  }
} 

 @media screen and (max-width: 540px) {
  header {
    height: 72vh;
    margin-top: 3rem; 
     
  }
   .header__socials,
  .scroll__down{
    display: none;
  }
}



